body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.background {
  position: absolute;
  width: 100%;
  height: 100%;
  background-attachment: fixed;
  background-image: url("https://i.pinimg.com/originals/d3/6d/46/d36d462db827833805497d9ea78a1343.jpg");
  background-size: 100% 100%;
}

.page {
  background-color: rgb(255, 255, 255);
  height: 100vh;
  position: absolute;
  left: 25%;
  width: 50%;
  min-height: 100vh;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.pageborder {
  padding: 2%;
}

.login-box {
  background-color: rgb(255, 255, 255);
  border: 5px solid black;
  position: absolute;
  width: 50%;
  left: 25%;
  top: 15%;
  height: 70%;
  text-align: center;
  vertical-align: middle;
  border-radius: 2rem;
}

.login-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.navbar {
  padding: 2%;
  background-color: #f43f5e;
  color: #ffffff;
  border: 5px solid #cc213d;
}

.menu-editor {
  width: 100%;
}

.menu-field {
  width: calc(100% - 25px);
}

.promo-table {
  width: 100%;
}

.promo-editor {
  height: 50vh;
  /* width: 25vw; */
}

.error-message {
  background: white;
  margin-top: 2%;
  font-size: 20px;
  border-radius: 5px;
  border: 5px solid rgb(255, 164, 164);
  padding: 2%;
}

.success-message {
  background: white;
  margin-top: 2%;
  font-size: 20px;
  border-radius: 5px;
  border: 5px solid rgb(142, 255, 148);
  padding: 2%;
}

.status-message-banner {
  display: flex;
  justify-content: right;
}

.logout-button {
  float: right;
}

.menu-item {
  background: #eeeeee;
  margin: 1%;
  padding: 5px;
  border-radius: 20px;
}

.menu-item-footer {
  display: flex;
  justify-content: right;
}

.menu-delete-button {
  float: right;
}

.menu-description {
  width: 99%;
  height: 20vh;
  resize: none;
}

input {
  padding: 5px;
  margin: 5px;
}

tr {
  background-color: #eeeeee;
}

th {
  padding: 10px;
}

td {
  padding: 10px;
}
